body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar{display:flex;border-bottom:1px solid black;box-shadow:0 2px 4px -2px grey;justify-content:space-between;align-items:center}.navbar h1{color:#18379B;font-size:35px;margin:3px}.navbar__left a{border:1px solid grey;border-radius:10px;padding:6px;margin-left:3px;margin-right:3px}footer{display:flex;justify-content:center;border-top:1px solid grey;margin-top:8px}.fourohfour{display:grid;margin:3px}.fourohfour h1{color:#18379B;font-size:50px;place-self:center;border:1px solid black;padding:40px;border-radius:20px;text-align:center}.fourohfour h1 p a{color:inherit;text-decoration:underline}.profile{display:flex;max-width:1000px;max-height:900px;justify-items:center;align-items:center;margin:auto;margin-top:10px}.profile__headshot{max-width:700px;max-height:500px;border-radius:10px}.profile__bio{font-size:21px;margin-left:8px;max-width:500px}.profile__bio a{text-decoration:underline;color:inherit}.project-grid{display:grid}.project-grid__tile{display:grid;color:grey}body{font-family:"Montserrat",sans-serif}.fab,.fas{color:grey;font-size:34px;padding-left:18px}a,a:hover,a:active,a:visited{color:grey;text-decoration:none}h1,h2,h3,h4{font-family:"Bodoni Moda",sans-serif}

